.home-page {
    h1 {
        text-align: center;
        position: absolute;
        left: 20%;
        top: 20%;
        width: 60%;
        max-height: 90%;
        color: #fff;
        font-size: 5rem;
        line-height: 53px;
        margin: 1;
        font-family: 'Coolvetica';
        font-weight: 400;
    }

    h2 {
        text-align: center;
        position: absolute;
        left: 20%;
        top: 30%;
        width: 60%;
        max-height: 90%;
        color: #fff;
        font-size: 4.5rem;
        line-height: 53px;
        margin: 1;
        font-family: 'sans-serif';
        font-weight: 400;
        
    }

    h3 {
        text-align: center;
        position: absolute;
        left: 20%;
        top: 10%;
        width: 60%;
        max-height: 90%;
        color: #fff;
        font-size: 4.5rem;
        line-height: 53px;
        margin: 1;
        font-family: 'La Belle Aurore';
        font-weight: 400;
    
    }
    .projects{
        //background-color: aqua;
        list-style-type: none;
        margin: 0;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 50%;
        
        a{
            display: flex;
            position: relative;
            text-decoration: none;
            width: 0%;
            height: 30%;
            right: 15%;
            margin-right: 20%;
            margin-left: 20%;
            h4 {
                text-align: center;
                white-space: nowrap;
                    position: relative;
                    height: 100%;
                    bottom: 20%;
                    left: 290%;
                    width: 100%;
                    color: #fff;
                    font-size: 3.0rem;
                    margin: 0;
                    font-family: 'sans-serif';
                    font-weight: 400;
                }
                
        }
        
    }
        
}